<template>
  <div class="tax-page tax-subject">
    <h4>个税项目</h4>
    <div class="content">
      <div class="options">
        <span>设置个税项目，个税项目是计税的最小单位，是我们总结的一套计算单元</span>
      </div>
      <table class="table sample-table table-bordered table-hover">
        <thead>
        <tr>
          <th scope="col" width="250">项目名称</th>
          <th scope="col" width="100">项目类型</th>
          <th scope="col">税法描述</th>
          <th scope="col" width="40">配置</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in pays" :key="item.id">
          <td>{{item.name}}</td>
          <td>{{item.includeTypeName}}</td>
          <td>{{item.subjectDesc}}</td>
          <td class="td-ops"><swak-link :to="'/system/taxitem/cfg/' + item.id" class="ops">配置</swak-link></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
  import Pays from '@/api/system/payItem';

  export default {
    name: "user",
    data() {
      return {
        pays: [],
      }
    },
    mounted() {
      this.$emit('layout:load', "taxitem")
      this.loadPays();
    },
    methods: {
      loadPays: function () {
        Pays.list().then(res => {
          this.pays = res.obj || [];
        })
      }
    }
  }
</script>

